<template>
   <div>
      <v-container class="container mt-0" fluid>
         <v-card color="accent" elevation="6">
            <v-row style="margin: 10px">
               <v-col cols="6">
                  <h3 class="warning-color">اجهزة الصرافة</h3>
               </v-col>

               <v-col cols="6" style="text-align: left">
                  <AddAtm
                     style="display: inline-block"
                     @reloadTask="initialData()"
                  />
               </v-col>
            </v-row>
         </v-card>
      </v-container>

      <v-container class="container" fluid>
         <v-card color="accent" elevation="6">
            <v-card-title class="warning-color">
               اجهزة الصرافة
               <v-spacer></v-spacer>
               <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="بحث"
                  single-line
                  hide-details
                  solo
               ></v-text-field>
            </v-card-title>

            <v-data-table
               :headers="headers"
               :items="ItemsFilter"
               :items-per-page="10"
               class="table-content table-warning-color"
            >
               <template v-slot:item.index="{ item, index }">
                  <h4 style="padding: 0 3px">{{ index + 1 }}</h4>
               </template>

               <!-- <template v-slot:item.actions="{ item }">
                  <UserPhotos />
               </template> -->
            </v-data-table>
         </v-card>
      </v-container>
   </div>
</template>

<script>
import AddAtm from "./../components/AddAtm.vue";
export default {
   components: {
      AddAtm,
   },
   data: () => ({
      atms: [],
      search: "",
      headers: [
         { text: "التسلسل", value: "index" },
         { text: "العنوان", value: "address" },
         { text: "اسم الصراف", value: "atmName" },
         { text: "رقم هاتف الدعم", value: "supportNumber" },
         { text: "خط الطول", value: "lang" },
         { text: "خط العرض", value: "lat" },
         //  { text: "الاجراءات", value: "actions" },
      ],
   }),

   //    gas - food - credit - transport - general
   created() {
      this.initialData();
   },
   methods: {
      initialData() {
         let self = this;
         self.$http
            .get(`${self.$store.state.apiUrl}/atmLocations`)
            .then((res) => {
               console.log(res.data);
               self.atms = res.data;
            });
      },
   },
   computed: {
      ItemsFilter() {
         if (this.search) {
            return this.atms.filter((item) => {
               return this.search
                  .toLowerCase()
                  .split(" ")
                  .every((v) => item.userName.toLowerCase().includes(v));
            });
         } else {
            return this.atms;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
